import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
	withStyles,
	Button,
	Card,
	CardContent,
	Checkbox,
	Divider,
	FormControl,
	FormControlLabel,
	TextField,
	Typography,
	Grid,
	Hidden,
	IconButton
} from '@material-ui/core';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Link } from 'react-router-dom';
import InputAdornment from '@material-ui/core/InputAdornment';
import { AccountCircle, Visibility, VisibilityOff } from '@material-ui/icons';
import { withRouter } from 'react-router-dom';
import { userActions } from '../../auth/store/actions/user.actions';

import imageRegister from '../../../assets/register.jpg';

import { styles } from '../index';

class AuthRegister extends Component {
	state = {
		user: this.props.dataToEdit ? this.props.dataToEdit.userName : '',
		email: this.props.dataToEdit ? this.props.dataToEdit.email : '',
		password: '',
		passwordConfirm: '',
		showPassword: false,
		showPasswordConfirm: false
	};

	static getDerivedStateFromProps(nextProps, prevState) {
		if (nextProps.userRegister && !nextProps.onlyForm) {
			nextProps.history.push({
				pathname: '/auth/confirm-account',
				email: prevState.email
			});
		}
		return null;
	}

	handleChange = event => {
		this.setState({
			[event.target.name]:
				event.target.type === 'checkbox' ? event.target.checked : event.target.value
		});
	};

	handleClickShowPassword = param => {
		this.setState(state => ({ [param]: !state[param] }));
	};

	canBeSubmitted() {
		const { user, email, password, passwordConfirm } = this.state;
		if (this.props.dataToEdit) {
			return user.length > 0 && email.length > 0;
		} else {
			return (
				user.length > 0 &&
				email.length > 0 &&
				passwordConfirm.length > 0 &&
				password.length > 0 &&
				password == passwordConfirm
			);
		}
	}

	validPassword = () => {
		const { password } = this.state;
		var passwordRegex = /^(?=.*[0-9])(?=.*[\^$*.[\]{}()?\-"!@#%&/\\,><':;|_~`+=])(?=.*[a-z])(?=.*[A-Z])([\w^$*.[\]{}()?\-"!@#%&/\\,><':;|_~`+=]{8,16})$/;
		return password.length && !passwordRegex.test(password);
	};

	renderForm = onlyForm => {
		const { classes } = this.props;
		const { user, email, password, passwordConfirm } = this.state;
		return (
			<form name='registerForm' className={classes.form} noValidate>
				<TextField
					className={classes.textField}
					label='User'
					autoFocus
					type='text'
					name='user'
					value={user}
					onChange={this.handleChange}
					variant='outlined'
					required
					fullWidth
				/>
				<TextField
					className={classes.textField}
					label='Email'
					autoFocus
					type='email'
					name='email'
					value={email}
					onChange={this.handleChange}
					variant='outlined'
					required
					fullWidth
				/>

				<TextField
					style={{ display: this.props.dataToEdit ? 'none' : 'unset' }}
					className={classes.textField}
					label='Password'
					type={this.state.showPassword ? 'text' : 'password'}
					name='password'
					value={password}
					onChange={this.handleChange}
					variant='outlined'
					required
					fullWidth
					error={this.validPassword()}
					InputProps={{
						endAdornment: (
							<InputAdornment position='end'>
								<IconButton
									aria-label='Toggle password visibility'
									onClick={() => this.handleClickShowPassword('showPassword')}
								>
									{this.state.showPassword ? <VisibilityOff /> : <Visibility />}
								</IconButton>
							</InputAdornment>
						)
					}}
				/>
				<TextField
					style={{ display: this.props.dataToEdit ? 'none' : 'unset' }}
					className={classes.textField}
					label='Password (Confirm)'
					type={this.state.showPasswordConfirm ? 'text' : 'password'}
					name='passwordConfirm'
					value={passwordConfirm}
					onChange={this.handleChange}
					variant='outlined'
					required
					fullWidth
					error={passwordConfirm.length && passwordConfirm != password}
					InputProps={{
						endAdornment: (
							<InputAdornment position='end'>
								<IconButton
									aria-label='Toggle password visibility'
									onClick={() => this.handleClickShowPassword('showPasswordConfirm')}
								>
									{this.state.showPasswordConfirm ? <VisibilityOff /> : <Visibility />}
								</IconButton>
							</InputAdornment>
						)
					}}
				/>
				<Button
					className={classes.buttonSubmit}
					variant='contained'
					color='primary'
					aria-label='CREATE AN ACCOUNT'
					disabled={!this.canBeSubmitted() || this.props.loadingRegister}
					onClick={() => {
						onlyForm
							? this.props.onSubmit({
									userName: this.state.user,
									email: this.state.email
							  })
							: this.props.handleRegisterUser(
									this.state.user,
									this.state.email,
									this.state.password
							  );
					}}
				>
					{this.props.loadingRegister && (
						<CircularProgress size={24} className={classes.buttonProgress} />
					)}
					{this.props.dataToEdit ? 'UPDATE ACCOUNT' : 'CREATE AN ACCOUNT'}
				</Button>
			</form>
		);
	};

	render() {
		const { classes, onlyForm } = this.props;

		return onlyForm ? (
			this.renderForm(onlyForm)
		) : (
			<Grid container className={classes.root}>
				<Hidden smDown>
					<Grid item xs={8} className={classes.leftPanel}>
						<img src={imageRegister} alt='logo' style={{ height: '100vh ' }} />
						<Typography
							className={classes.titleForm}
							variant='h6'
							style={{
								position: 'absolute',
								bottom: '0%',
								left: '50px',
								margin: '0px',
								color: 'rgb(24, 31, 79)'
							}}
						>
							CREATE AN ACCOUNT FOR PUBLISH YOUR PRODUCTS
						</Typography>
					</Grid>
				</Hidden>
				<Grid item xs={12} sm={8} md={4} className={classes.rightPanel}>
					<Typography className={classes.titleForm} variant='h6'>
						CREATE AN ACCOUNT
					</Typography>

					{this.renderForm()}

					<div style={{ paddingTop: '2rem', paddingBottom: '1rem' }}>
						<Link className={classes.links} to='/login'>
							Login
						</Link>
					</div>
				</Grid>
			</Grid>
		);
	}
}

const mapStateToProps = state => {
	return {
		loadingRegister: state.authReducers.loadingRegister,
		userRegister: state.authReducers.userRegister
	};
};
const mapDispatchToProps = dispatch => {
	return {
		handleRegisterUser: (user, email, password, onlyForm) => {
			dispatch(userActions.RegisterUser({ user, email, password }, onlyForm));
		}
	};
};

export default withRouter(
	connect(
		mapStateToProps,
		mapDispatchToProps
	)(withStyles(styles, { withTheme: true })(AuthRegister))
);
